 

.categories{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
    margin-top: 3em;
}

.categories div{
    width: 200px;
    max-height: 250px;
    overflow: hidden;
} 

.categories img {
    width: 100%;
    height: 200px;
    object-fit: cover; 
    border-radius: 0.3em;
  }

  .categories p{
    text-align: center;
  }

  .categories button{
    background-color: orange;
    padding: 1em 0em;
    width: 100%; 
    border-radius: 0.1em;
    border: none;
  }