.whychooseeva{
    width: 70%;
    margin-top: 5em;
    min-height: 50vh;
}

.whychooseeva h1{
    text-align: center;
}

.whychooseeva .sec{
    margin-top: 2em;
    min-height: 400px;
    display: flex;
    flex-wrap: wrap;
  }

  .sec > div{
    box-sizing: border-box;
    text-align: center;
    background-color: orange;
    min-height: 400px;
    width: 23%;
    margin-right: 2%;
    margin-bottom: 2em;
    padding: 1em;
  }

  .sec  > div div{
    margin-top: 3em;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    background-color: white;
  }

  .whychooseeva img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }
  

  @media screen and (max-width: 1000px){
    .whychooseeva{
      margin-top: 3em;
  }
  .sec > div{
      width: 45%;
      margin-right: 5%;
    }
  }

  @media screen and (max-width: 700px){
    .sec > div{
      width: 100%;
      margin-right: 0%;
    }
  }